.wrapper {
  position: relative;
  z-index: 1; // üî• Ï§ëÏöî
  width: 100%;
  margin: 0 auto;
  .content {
    position: relative;
    z-index: 2; // ÏïàÏ†ÑÌïòÍ≤å Ìïú Îã®Í≥Ñ ÏúÑ
    min-height: 100vh;
  }
  /* Î∞∞Í≤Ω ÏòÅÏÉÅ */
  .video-bg {
    position: fixed;
    inset: 0;
    z-index: 0;

    width: 100vw;
    height: 100vh;
    overflow: hidden;

    video {
      position: absolute;
      top: 50%;
      left: 50%;

      min-width: 100%;
      min-height: 100%;

      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
}
