$tablet: 768px;
@mixin tablet {
  @media all and (min-width: $tablet) {
    @content;
  }
}

//Îç∞Ïä§ÌÅ¨ÌÉë Í∏∞Ï§Ä Î∞òÏùëÌòï
$desktop: 1024px;
@mixin desktop {
  @media all and (min-width: $desktop) {
    @content;
  }
}

//Î†àÏù¥ÏïÑÏõÉ ÌîåÎ†âÏä§
@mixin flex-layout($dir: row, $just: center, $align: center, $gap: 1rem) {
  display: flex;
  flex-direction: $dir;
  justify-content: $just;
  align-items: $align;
  gap: $gap;
}
